import Task from "./Task";
import "../css/tasks.css";

function Tasks({ tasks, onDelete }) {
  return (
    <div className="tasksContainer">
      {tasks.map((task) => (
        <Task text={task.text} id={task.id} description={task.description} onDelete={onDelete} />
      ))}
    </div>
  );
}

export default Tasks;
